import React from "react";
import "./AnimeItem.css";


function AnimeItem(props) {
  const {anime, onAnimeClickOpen} = props;

  return (
     <div className="anime-item">
      <img src={anime.thumbnailUrl} className="anime-img" onClick={()=> onAnimeClickOpen(anime)}/>
      <h4>{anime.title}</h4>
     </div>
  );
}

export default AnimeItem;
