import React from "react";
import "./App.css";
import AnimeItem from "./components/AnimeItem";
import AnimePost from "./components/AnimePost";
import AppHeader from "./components/AppHeader";
import animes from "./data/animes";
import { useState } from "react";
import AppSearch from "./components/AppSearch";

function App() {
  const [selectedAnime, setSelectedAnime] = useState(null);
  const [searchText, SetSearchText] = useState("");

  function onAnimeClick(anime) {
    setSelectedAnime(anime);
  }

  const filteredAnime = animes.filter((anime) =>
    anime.title.includes(searchText.toUpperCase())
  );
  const animeElements = filteredAnime.map((anime, index) => {
    return (
      <AnimeItem key={index} anime={anime} onAnimeClickOpen={onAnimeClick} />
    );
  });

  let animePost = null;
  //if selectedAnime have values
  if (selectedAnime) {
    animePost = (
      <AnimePost anime={selectedAnime} onAnimeClickClose={onAnimeClick} />
    );
  }

  return (
    <div className="app">
      <AppHeader />
      <section className="app-section">
        <div className="app-container">
          <AppSearch value={searchText} onValueChange={SetSearchText} />
          <div className="app-grid">{animeElements}</div>
        </div>
      </section>
      {animePost}
    </div>
  );
}

export default App;
