import React from 'react';
import './AnimePost.css';

function AnimePost(props) {

    const {anime, onAnimeClickClose} = props;

      
    return (
        <div className="anime-post">
            <div className="anime-post-bg" onClick={()=> onAnimeClickClose(null)}></div>
            <div className="anime-post-content">
                <img src={anime.thumbnailUrl} onClick={()=> onAnimeClickClose(null)} />
                <h4>{anime.title}</h4>
            </div>
        </div>
    )
}

export default AnimePost
